@import url("https://fonts.googleapis.com/css2?family=Tajawal:wght@300;400&display=swap");
@import url(https://fonts.googleapis.com/css?family=Lato);
@import url(https://cdnjs.cloudflare.com/ajax/libs/font-awesome/4.6.3/css/font-awesome.css);
$primary-color: #e6a743;
$light-color: rgb(247, 247, 248);
$base-color: rgb(250, 179, 0);
$yellowish-color: #fafbf0;
$heading-color: #e6a743;
$logo-color:#f1990c;
$border-gradient:linear-gradient(to left, #f2b61f, #f2c98d);
$border-cross-color:#f2b61f;


* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: "Tajawal",  Helvetica, Arial, Sans-Serif;
}
body {
  background-color: $yellowish-color;
  overflow-x: hidden;
  box-sizing: content-box; /* So the width will be 100% + 17px */

}
a {
  text-decoration: none;
  width: 100%;
}
a:link,
a:visited {
  color: $light-color;
}

a:hover {
  color: $light-color;
}
ul {
  list-style: none;
}
