// style.scss
@import "defaults";
$media-laptops: "only screen and (max-width : 1024px)";
$media-laptops-sm: "only screen and (max-width : 928px)";
$media-tablet: "only screen and (max-width : 768px)";
$media-mobile: "only screen and (max-width : 600px)";
$media-mobile-sm: "only screen and (max-width : 480px)";
$media-mobile-xsm: "only screen and (max-width : 353px)";

.container {
  max-width: 1300px;
  margin: auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

#header {
  width: 100%;
  max-width: 1300px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: fixed;
  top: 0;
  z-index: 250;
  transition: top 0.3s ease-in;
  background: linear-gradient(
    to top,
    rgba(0, 0, 0, 0) 0%,
    rgba(0, 0, 0, 0.844) 100%
  );
  padding: 15px 70px;
  @media #{$media-mobile} {
    padding: 15px 10px;
  }
  #logo-img {
    width: 120px;
    cursor: pointer;
  }
  .nav-area {
    display: flex;
    justify-content: center;
  }
  .nav-area li a {
    @media #{$media-mobile} {
      padding: 10px;
    }
    display: block;
    padding: 0 15px;
    text-align: right;
    font-weight: bold;
    color: white;
    transition: transform 0.2s ease-in;
    &:hover {
      transform: scale(1.05);
    }
  }
}

#top-section {
  background-color: $primary-color;
  width: 100%;
  height: 200;
  .top-section-pattern {
    height: 90px;
    background: url(/assets/pathern-slateblue.svg) repeat-x bottom center
      $primary-color;
    opacity: 0.5;
  }
  .top-section-bottom-pattern {
    height: 90px;
    background: url(/assets/pathern-slateblue.svg) repeat-x bottom center
      $primary-color;
    opacity: 0.5;
    transform: scale(-1);
  }
}
///
#main-section {
  background-color: $yellowish-color;
  width: 100%;
  height: 900px;
  display: flex;
  align-items: center;
  flex-direction: column;
}

#card {
  margin-top: -200px;
  position: relative;
  opacity: 0;
  transform: translateY(100px);
  transition: all 0.5s ease-in;
  .card-background {
    // height: 450px;
    @media #{$media-tablet} {
      width: 550px;
    }
    @media #{$media-mobile} {
      width: 450px;
      height: 380px;
    }
    @media #{$media-mobile-sm} {
      width: 330px;
      height: 380px;
    }
    @media #{$media-mobile-xsm} {
      width: 310px;
      height: 410px;
    }

    width: 700px;
    object-fit: cover;
    border: 10px solid transparent;
    padding: 5px;
    border: 5px solid yellow;
    border-image-slice: 1;
    border-image-source: $border-gradient;
    box-shadow: 1px 4px 8px 0px rgba(0, 0, 0, 0.25);
    -webkit-box-shadow: 1px 4px 8px 0px rgba(0, 0, 0, 0.25);
    -moz-box-shadow: 1px 4px 8px 0px rgba(0, 0, 0, 0.25);
  }
  #card-content {
    z-index: 150;
    position: absolute;
    top: 0;
    right: 0;
    margin: 7%;
    width: 65%;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: right;

    @media #{$media-tablet} {
      width: 78%;
    }

    @media #{$media-mobile} {
      width: 78%;
    }

    @media #{$media-mobile-sm} {
      width: 80%;
    }
    h1 {
      color: $heading-color;
      @media #{$media-tablet} {
        font-size: 30px;
        margin-top: 10px;
        text-align: center;
      }
      @media #{$media-mobile} {
        font-size: 25px;
        margin-top: 12px;
        text-align: center;
      }
      @media #{$media-mobile-sm} {
        font-size: 22px;
        margin-top: 12px;
        text-align: center;
      }
      @media #{$media-mobile-xsm} {
        font-size: 21px;
        margin-top: 10px;
      }
    }
    span {
      color: $heading-color;
      font-weight: bold;
    }
    p {
      font-size: 20px;
      text-align: right;
      margin-top: 30px;
      @media #{$media-tablet} {
        margin-top: 18px;
        font-size: 20px;
      }
      @media #{$media-mobile} {
        margin-top: 18px;
        font-size: 19px;
      }
      @media #{$media-mobile-sm} {
        margin-top: 15px;
        font-size: 17px;
      }
    }
    #regards {
      font-size: 22px;
      margin-top: 40px;
      color: $heading-color;
      text-align: center;
      @media #{$media-tablet} {
        font-size: 21px;
        margin-top: 20px;
      }

      @media #{$media-mobile} {
        font-size: 18px;
        margin-top: 20px;
      }
      @media #{$media-mobile-sm} {
        font-size: 18px;
        margin-top: 20px;
      }
    }
    #brand-logo {
      width: 60px;
      margin: 10px;
      @media #{$media-mobile} {
        width: 50px;
      }
      @media #{$media-mobile-sm} {
        width: 50px;
      }
    }
  }
}
///
.heading {
  padding: 22px;
  margin-top: 15px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;

  h1 {
    padding-bottom: 10px;
    color: $heading-color;
  }
  img {
    width: 250px;
    align-self: center;
  }
}

.service-card {
  display: flex;
  justify-content: space-evenly;
  margin: 20px 0px;
  width: 95%;
  padding: 50px;

  @media #{$media-laptops-sm} {
    width: 100%;
  }
  @media #{$media-mobile} {
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 30px;
  }
  @media #{$media-mobile} {
    margin: 0px 0px;
  }

  .card-text {
    text-align: right;
    line-height: 25px;
    @media #{$media-mobile} {
      text-align: center;
      line-height: 32px;
    }

    h1 {
      margin-bottom: 25px;
      max-width: 380px;
      color: $heading-color;
      line-height: 35px;
      @media #{$media-laptops-sm} {
        font-size: 28px;
      }
      @media #{$media-tablet} {
        font-size: 23px;
        max-width: 300px;
      }
      @media #{$media-mobile} {
        text-align: center;
        margin-bottom: 15px;
        max-width: 100%;
        margin-top: 15px;
        font-size: 28px;
      }
      @media #{$media-mobile-sm} {
        font-size: 25px;
      }
      @media #{$media-mobile-xsm} {
        font-size: 20px;
      }
    }

    li {
      font-size: 20px;
    }
    li::after {
      // background-color: red;
      content: " -";
      font-weight: bold;
      width: 22px;
      height: 22px;
      flex-shrink: 0;
    }
  }

  .card-image {
    position: relative;
    img {
      width: 250px;
      height: 250px;
      object-fit: cover;
      border: 10px solid transparent;
      padding: 5px;
      border: 5px solid yellow;
      border-image-slice: 1;
      border-image-source: $border-gradient;
      @media #{$media-tablet} {
        width: 200px;
        height: 200px;
      }
      @media #{$media-mobile} {
        width: 290px;
        height: 290px;
      }
      @media #{$media-mobile-xsm} {
        width: 240px;
        height: 240px;
      }
    }
  }
  .card-image::after {
    content: "";
    position: absolute;
    width: 85%;
    height: 85%;
    left: 5%;
    top: 5%;
    transform: rotate(45deg);
    border: 5px solid $border-cross-color;
    @media #{$media-mobile} {
      border: none;
    }
  }
}

.pattern {
  width: 100%;
  height: 90px;
  flex-shrink: 0;
  border-top: 2px solid #ddd;
  background: url(/assets/pathern-white.svg) repeat-x bottom center
    $yellowish-color;
}

.top-pattern {
  width: 100%;
}
.bottom-pattern {
  width: 100%;
  height: 90px;
  margin-top: 100px;
  flex-shrink: 0;
  border-top: 2px solid #ddd;
  background: url(/assets/pathern-white.svg) repeat-x top center
    $yellowish-color;
  transform: rotate(180deg);
}

#location-ifame {
  opacity: 0;
  transform: translateY(100px);
  transition: all 0.5s ease-in;
  width: 80%;
  flex-shrink: 0;
  height: 400px;
  background-color: rgba(236, 236, 236, 0.555);
  padding: 5px;
  border: 5px solid yellow;
  border-image-slice: 1;
  margin: 25px 0;
  margin-top: 15px;
  margin-bottom: 60px;
  border-image-source: $border-gradient;
  @media #{$media-mobile} {
    width: 90%;
    height: 350px;
  }
}

#contact-us .info a {
  color: rgb(34, 34, 34);
  cursor: pointer;
  font-size: 22px;
  &:hover {
    opacity: 0.5;
  }
  @media #{$media-mobile} {
    font-size: 28px;
  }
}
#contact-us {
  display: flex;
  justify-content: center;
  align-items: flex-start;
  width: 80%;
  @media #{$media-mobile} {
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }

  .info {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    @media #{$media-mobile} {
      margin: 0px 20px;
      padding: 12px 0;
    }
    margin: 0px 52px;

    h3 {
      color: $heading-color;
      font-size: 25px;
      padding: 15px 0;
    }
  }
}

footer {
  background-color: $heading-color;
  width: 100%;
  padding: 20px 10px;
  color: white;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;

  @media #{$media-mobile-xsm} {
    font-size: 12px;
  }
  // margin-top: 100px;
  a {
    width: auto;
    padding: 0 10px;
    font-weight: bold;
    cursor: pointer;
  }
}

.arrow {
  text-align: center;
}

.arrow-parent {
  background-color: transparent;
  position: relative;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  top: -30px;
}
.bounce {
  position: absolute;
  z-index: 300;
  // top: -45px;
  // left: 50%;
  -moz-animation: bounce 3s infinite;
  -webkit-animation: bounce 3s infinite;
  animation: bounce 3s infinite;
}

@keyframes bounce {
  0%,
  20%,
  50%,
  80%,
  100% {
    transform: translateY(0);
  }
  40% {
    transform: translateY(-30px);
  }
  60% {
    transform: translateY(-15px);
  }
}

@keyframes rot {
  0% {
    transform: rotateY(0deg);
  }
  40% {
    transform: rotateY(0deg);
  }
  100% {
    transform: rotateY(360deg);
  }
}

.caousel {
  position: relative;
  height: 65vh;
  width: 100%;
  flex-shrink: 0;
  margin: 0 auto;
  background-color: $heading-color;
  .carousal__image {
    width: 100%;
    height: 100%;
    object-fit: cover;
    filter: blur(8px);
    -webkit-filter: blur(5px);
  }

  .carousel__track-container {
    background-color: $heading-color;
    height: 100%;
    position: relative;
    overflow: hidden;
    border-bottom-left-radius: 15px;
    border-bottom-right-radius: 15px;
  }

  .carousel_track {
    padding: 0;
    margin: 0;
    list-style: none;
    height: 100%;
    position: relative;
    transition: transform 1600ms ease-in-out;
  }

  .carousel__slide {
    position: absolute;
    top: 0;
    bottom: 0;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .carousel__button {
    position: absolute;
    // top: 50%;
    // transform: translateY(-50%);
    // height: 200px;
    // background-color: black;
    // opacity: 0.4;
    // color: white;
    display: none;
  }

  #carousel__button_right {
    // right: 30px;
    // z-index: 999;
  }

  #carousel__button_left {
    // left: 30px;
    // z-index: 999;
  }
}

.is-hidden {
  display: none;
}

.caption-container {
  position: absolute;
  width: 600px;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  z-index: 200;
  background-color: rgba(0, 0, 0, 0.637);

  padding: 20px;
  text-align: center;
  border: 5px solid yellow;
  border-image-slice: 1;
  border-image-source: $border-gradient;
  box-shadow: 1px 4px 8px 0px rgba(0, 0, 0, 0.349);
  -webkit-box-shadow: 1px 4px 8px 0px rgba(0, 0, 0, 0.349);
  -moz-box-shadow: 1px 4px 8px 0px rgba(0, 0, 0, 0.349);
  background: url(/assets/pathern-black.svg) repeat-x bottom center
    rgba(0, 0, 0, 0.508) blur(0px) saturate(50%);
  backdrop-filter: blur(10px);
  @media #{$media-mobile} {
    width: 90%;
    // margin-top: 50%;
  }

  h1 {
    color: white;
    padding: 10px;
    font-size: 35px;
    @media #{$media-mobile} {
      font-size: 30px;
    }
  }
  h3 {
    color: #f1990c;
    font-size: 22px;
    @media #{$media-mobile} {
      font-size: 18px;
    }
  }
}

/* Color Variables */
$color-email: #ea4335;
$color-whatsapp: #25d366;
$color-phone: #34b7f1;
$color-facebook: #3b5a9b;

/* Social Icon Mixin */
@mixin social-icon($color) {
  background: $color;
  color: #fff;

  .tooltip {
    background: $color;
    color: currentColor;

    &:after {
      border-top-color: $color;
    }
  }
}

/* Social Icons */
.social-icons {
  display: flex;
}

.social-icon {
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  width: 60px;
  height: 60px;
  margin: 0 0.5rem;
  border-radius: 50%;
  cursor: pointer;
  font-family: "Helvetica Neue", "Helvetica", "Arial", sans-serif;
  font-size: 30px;
  text-decoration: none;
  transition: all 0.15s ease;
  color: #000;

  &--whatsapp {
    @include social-icon($color-whatsapp);
  }
  &--phone {
    @include social-icon($color-phone);
  }
  &--facebook {
    @include social-icon($color-facebook);
  }
  &--email {
    @include social-icon($color-email);
  }

  i {
    position: relative;
    top: 1px;
  }
}

.fade-up {
  opacity: 0;
  transform: translateY(100px);
  transition: transform 0.5s ease-in;
}


